import Cookie from 'js-cookie'
import router from '../router/index'
export default {
	state: {
		routes: [],
		menu: [],
		currentMenu: null,
		breadCrumbs: [],
		isCollapse: false
	},
	mutations: {
		setMenu(state, menu) {
			state.menu = menu
			Cookie.set('menu', JSON.stringify(menu))
		},
		clearMenu(state) {
			state.menu = []
			Cookie.remove('menu')
		},
		setBreadCrumbs(state, arr) {
			state.breadCrumbs = []
			state.breadCrumbs.push(...arr)
		},
		getRoutes(state) {
			if (!Cookie.get('menu')) return
			const menu = JSON.parse(Cookie.get('menu'))
			state.menu = menu
			const routes = [
				{
					path: '/',
					name: 'index',
					redirect: { name: 'home' },
					meta: {
						label: '首页'
					},
					component: () => import('@/views/Index'),
					children: []
				}
			]
			menu.forEach(item => {
				if (item.children) {
					item.children = item.children.map(item => {
						// item.component = () => import(`@/views/${item.url}`)
						item.component = resolve => require([`@/views/${item.url}`], resolve)
						return item
					})
					routes[0].children.push(...item.children)
				} else {
					// item.component = () => import(`@/views/${item.url}`)
					item.component = resolve => require([`@/views/${item.url}`], resolve)
					routes[0].children.push(item)
				}
			})
			state.routes = routes
			router.options.routes = routes
			router.addRoutes(routes)
		},
		selectMenu(state, val) {
			state.currentMenu = val
		},
		toggleCollapse(state, val) {
			state.isCollapse = val
		}
	},
	actions: {}
}
