import axios from 'axios'
import router from '../router'
import store from '../store'
import { Loading, Message } from 'element-ui'
import Util from '@/util'

const options = {
	lock: true,
	text: '正在加载中。。。',
	spinner: 'el-icon-loading',
	background: 'rgba(0, 0, 0, 0.4)'
}

/**
 * 创建一个axios实例
 */
const _axios = axios.create({
	timeout: 3000,
	baseURL: process.env.VUE_APP_API,
	method: 'post'
})
let loadingInstance = null

/**
 * 添加请求拦截器
 */
_axios.interceptors.request.use(
	config => {
		loadingInstance = Loading.service(options)
		const noAuthorizationApis = ['login', 'register', 'sendMsg']
		if (noAuthorizationApis.indexOf(config.url.split('/')[2] !== -1)) {
			config.headers.AUTHORIZATION = new Util().getAuth(store.state.user.token)
		}
		return config
	},
	err => {
		loadingInstance.close()
		return Promise.reject(err)
	}
)

/**
 * 添加响应拦截器
 */

_axios.interceptors.response.use(
	response => {
		loadingInstance.close()
		if (response.status === 200) {
			console.log(response.data)
			return response.data
		} else {
			Message({
				message: response.data.msg,
				type: 'warning'
			})
			if (response.status === 401) {
				router.replace({ path: '/login' })
			}
			return Promise.reject(new Error())
		}
	},
	err => {
		loadingInstance.close()
		Message({
			message: err.response ? err.response.status + ':' + err.response.data : err.message,
			type: 'error'
		})
		return Promise.reject(
			new Error({
				status: err.response.status,
				msg: err.response.data
			})
		)
	}
)

export default _axios
