import { Message } from 'element-ui'
import { Base64 } from 'js-base64'
class Util {
	constructor() {}
	/**
	 * 校验手机号
	 * @param {Object} rule
	 * @param {String} value
	 * @param {Function} callback
	 */
	validateUsername(rule, value, callback) {
		if (value === '') {
			if (callback) callback(new Error('填写手机号码'))
		} else if (!/^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/.test(value)) {
			if (callback) callback(new Error('手机号码格式不正确'))
		} else {
			if (callback) callback()
		}
	}

	/**
	 * 校验密码
	 * @param {Object} rule
	 * @param {String} value
	 * @param {Function} callback
	 */
	validatePass(rule, value, callback) {
		if (value === '') {
			if (callback) callback(new Error('请填写密码'))
		} else if (!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/.test(value)) {
			if (callback) callback(new Error('密码格式不正确'))
		} else {
			if (callback) callback()
		}
	}

	/**
	 * 校验验证码
	 * @param {Object} rule
	 * @param {String} value
	 * @param {Function} callback
	 */
	validateCode(rule, value, callback) {
		if (value === '') {
			if (callback) callback(new Error('请填写验证码'))
		} else if (!/^\d{4}$/.test(value)) {
			if (callback) callback(new Error('请输入四位验证码'))
		} else {
			if (callback) callback()
		}
	}

	/**
	 * 获取auth
	 * @param {String} val:token
	 * @returns
	 */
	getAuth(val) {
		return 'Basic ' + Base64.encode(val)
	}
}

export default Util
